  }
  .buttons button{
    /* padding: 14px 23px;
    font-size: 23px; */
    border: 1px solid white;
    background: coral;
    cursor: pointer;
    color: white;
    font-weight: 500;
    cursor: pointer;
    width: 150px;
    font-size: 15px;
}
.buttons a{
    text-decoration: none;
}

  @media screen and (max-width: 480px) {
    .i {
      flex-direction: column;
    }
  
    .i-left-wrapper {
    
      padding: 10px;
      align-items: center;
      justify-content: center;
      text-align: center;
      height: 100%;
    }
  
    .i-desc{
      display: none;
    }
    .i-img {
      width: 70%;
